const util = {}

/**
 * @description 
 * @param {String} title 
 */
util.title = function (title) {
  window.document.title = title || 'bin-ui'
}

/**
 * @description 
 * @param {String} url 
 */
util.open = function (url, target) {
  let a = document.createElement('a')
  a.setAttribute('href', url)
  if (target) {
    a.setAttribute('target', '_blank')
  }
  a.setAttribute('id', 'b-link-temp')
  document.body.appendChild(a)
  a.click()
  document.body.removeChild(document.getElementById('b-link-temp'))
}

/**
 * 
 * @param time
 * @param cFormat
 * @returns {*}
 */
util.parseTime = function (time, cFormat) {
  if (arguments.length === 0) {
    return null
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000
    date = new Date(time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  // eslint-disable-next-line
  const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key]
    if (key === 'a') return ['', '', '', '', '', '', ''][value - 1]
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  // eslint-disable-next-line
  return time_str
}

// 
function _now () {
  return +new Date()
}

/**
 *  waitfunc 
 *
 * @param  {function} func        
 * @param  {number}   wait        
 * @param  {boolean}  immediate   ture
 * @return {function}             
 */
util.debounce = function (func, wait = 50, immediate = true) {
  let timer, context, args
  // 
  const later = () => setTimeout(() => {
    // 
    timer = null
    // 
    // 
    if (!immediate) {
      func.apply(context, args)
      context = args = null
    }
  }, wait)

  // 
  return function (...params) {
    // later
    if (!timer) {
      timer = later()
      // 
      // 
      if (immediate) {
        func.apply(this, params)
      } else {
        context = this
        args = params
      }
      // later
      // 
    } else {
      clearTimeout(timer)
      timer = later()
    }
  }
}

/**
 * underscore func   / wait
 *
 * @param  {function}   func      
 * @param  {number}     wait      
 * @param  {object}     options   {leading: false}
 *                                {trailing: false}
 *                                
 * @return {function}             
 */
util.throttle = function (func, wait, options = {}) {
  let context, args, result
  let timeout = null
  // 
  let previous = 0
  //  options 
  if (!options) options = {}
  // 
  let later = function () {
    //  leading previous  0
    //  if 
    previous = options.leading === false ? 0 : _now()
    // 
    timeout = null
    result = func.apply(context, args)
    if (!timeout) context = args = null
  }
  return function () {
    // 
    let now = _now()
    //  true
    // 
    // 
    //  remaining 0
    if (!previous && options.leading === false) previous = now
    // 
    let remaining = wait - (now - previous)
    context = this
    args = arguments
    //  + wait
    // 
    //  trailing
    //  leading
    //  if 
    // 
    // 2
    // 2.2
    if (remaining <= 0 || remaining > wait) {
      // 
      if (timeout) {
        clearTimeout(timeout)
        timeout = null
      }
      previous = now
      result = func.apply(context, args)
      if (!timeout) context = args = null
    } else if (!timeout && options.trailing !== false) {
      //  trailing
      // 
      //  leading  trailing
      timeout = setTimeout(later, remaining)
    }
    return result
  }
}

/**
 * 
 * @param arr 
 */
util.shuffle = function (arr) {
  let newArr = arr.slice()// 
  for (let i = 0; i < newArr.length; i++) {
    let j = util.getRandomInt(0, i)// 0-
    let t = newArr[i]
    newArr[i] = newArr[j]
    newArr[j] = t
  }
  return newArr
}

/**
 * 
 * @param min 
 * @param max 
 * @return {number}
 */
util.getRandomInt = function (min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

/**
 * 
 * @param str
 * @returns {*}
 */
util.replaceReturn = function (str) {
  return str ? str.replace(/()+|(\n)+|(\r\n)+/g, '') : ''
}

/**
 * canvas
 */
util.getWaterMark = function (str) {
  let waterMarkText = str || 'water-mark'

  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')
  canvas.width = canvas.height = 100
  ctx.textAlign = 'center'
  ctx.textBaseline = 'middle'
  ctx.globalAlpha = 0.08
  ctx.font = '20px Microsoft Yahei'
  ctx.translate(50, 50)
  ctx.rotate(-Math.PI / 4)
  ctx.fillText(waterMarkText, 0, 0)
  return canvas
}

// 
export function oneOf (value, validList) {
  return validList.indexOf(value) > -1
}

// Find components upward
export function findComponentUpward (context, componentName, componentNames) {
  if (typeof componentName === 'string') {
    componentNames = [componentName]
  } else {
    componentNames = componentName
  }

  let parent = context.$parent
  let name = parent.$options.name
  while (parent && (!name || componentNames.indexOf(name) < 0)) {
    parent = parent.$parent
    if (parent) name = parent.$options.name
  }
  return parent
}

// Find component downward
export function findComponentDownward (context, componentName) {
  const childrens = context.$children
  let children = null

  if (childrens.length) {
    for (const child of childrens) {
      const name = child.$options.name
      if (name === componentName) {
        children = child
        break
      } else {
        children = findComponentDownward(child, componentName)
        if (children) break
      }
    }
  }
  return children
}

// Find components downward
export function findComponentsDownward (context, componentName) {
  return context.$children.reduce((components, child) => {
    if (child.$options.name === componentName) components.push(child)
    const foundChilds = findComponentsDownward(child, componentName)
    return components.concat(foundChilds)
  }, [])
}

// Find components upward
export function findComponentsUpward (context, componentName) {
  let parents = []
  const parent = context.$parent
  if (parent) {
    if (parent.$options.name === componentName) parents.push(parent)
    return parents.concat(findComponentsUpward(parent, componentName))
  } else {
    return []
  }
}

function typeOf (obj) {
  const toString = Object.prototype.toString
  const map = {
    '[object Boolean]': 'boolean',
    '[object Number]': 'number',
    '[object String]': 'string',
    '[object Function]': 'function',
    '[object Array]': 'array',
    '[object Date]': 'date',
    '[object RegExp]': 'regExp',
    '[object Undefined]': 'undefined',
    '[object Null]': 'null',
    '[object Object]': 'object'
  }
  return map[toString.call(obj)]
}

export function deepCopy (data) {
  const t = typeOf(data)
  let o

  if (t === 'array') {
    o = []
  } else if (t === 'object') {
    o = {}
  } else {
    return data
  }

  if (t === 'array') {
    for (let i = 0; i < data.length; i++) {
      o.push(deepCopy(data[i]))
    }
  } else if (t === 'object') {
    for (let i in data) {
      o[i] = deepCopy(data[i])
    }
  }
  return o
}

export default util
